export default {
	props:{
		items:{
			type: Array
		},
	    initValue: {
			type: String,
			default: '1'
		}
	},
	data() {
		return {
			index: this.initValue
		};
	},
	methods:{
		buttonChange:function(id){
			this.index = id
			this.$emit("buttonChange",id)
		}
	}
}
